body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #efefef;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todoContainer{
    width: 500px;
    margin: 100px auto;

    display: flex;
    flex-direction: column;
}

.todoCreateForm{
    display: flex;
    grid-gap: 5px;
    gap: 5px;
}

.todoInput{
    flex-grow: 1;
    width: 100%;
    padding: 10px;
    border-radius: 3px;
    border: solid 1px #999;
}

.buttonCreate{
    border: none;
    border-radius: 3px;
    background-color: rgb(26, 76, 169);
    color: white;
    padding: 10px 20px;
}

.todosContainer{
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
    margin-top: 10px;
}

.todo{
    padding: 10px;
    background-color: white;
}

.todoTitle{
    flex: 1 1;
    font-size: 20px;
}

.todoUpdateForm, .todoInfo{
    display: flex;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
}

.button{
    border: none;
    border-radius: 3px;
    background-color: rgb(72, 122, 221);
    color: white;
    padding: 10px 20px;
}

.buttonDelete{
    border: none;
    border-radius: 3px;
    background-color: rgb(169, 26, 26);
    color: white;
    padding: 10px 20px;
}
